<template>
    <div id="artist-grid">
        <ul class="thumb_container">
            <li
                v-for="oneArtist in artistList"
                v-bind:key="oneArtist.member_id"
            >
                <div class="artistcard">
                    <img
                        :src="getPic(oneArtist)"
                        v-bind:alt="oneArtist.PublicLastName"
                        height="170"
                        width="170"
                    />

                    {{ oneArtist.member_id }}
                    {{ oneArtist.PublicFirstName }}
                    {{ oneArtist.PublicLastName }}

                    <div v-if="checkIfInItinerary(oneArtist.member_id)">
                        <div v-if="checkIfVisited(oneArtist.member_id)">
                            <div
                                v-if="
                                    itineraryDetailsById[oneArtist.member_id]
                                        .rating == null
                                "
                            >
                                Visited/ Not Rated
                            </div>
                            <div v-else>
                                Rating :
                                {{
                                    itineraryDetailsById[oneArtist.member_id]
                                        .rating
                                }}
                            </div>
                        </div>
                        <div v-else>
                            <button
                                v-on:click="
                                    removeFromItinerary(oneArtist.member_id)
                                "
                            >
                                <b> Remove ❌ </b>
                            </button>
                        </div>
                    </div>
                    <div v-else>
                        <button
                            v-on:click="addToItinerary(oneArtist.member_id)"
                        >
                            <b> Add ✅️ </b>
                        </button>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    methods: {
        // load picture (just setting the atr with vue will not work)
        getPic(artistData) {
            return (
                this.imgBaseUrl +
                "/" +
                artistData["thumb_data"]["filename_and_path"]
            );
        },

        addToItinerary(memberID) {
            this.$emit("add-to-itinerary", memberID);
        },

        removeFromItinerary(memberID) {
            this.$emit("remove-from-itinerary", memberID);
        },

        checkIfInItinerary(memberID) {
            return this.memberIdsOfItinerary.includes("" + memberID);
        },
        checkIfVisited(memberID) {
            if (
                this.itineraryDetailsById &&
                this.itineraryDetailsById[memberID]
            ) {
                if (this.itineraryDetailsById[memberID].visited == "1") {
                    return true;
                }
            }
            return false;
        },
    },

    computed: {
        memberIdsOfItinerary() {
            var filtered = [];
            for (var i = 0; i < this.itineraryDetails.length; i++) {
                filtered.push("" + this.itineraryDetails[i]["member_id"]);
            }
            return filtered;
        },
    },

    props: {
        artistList: {
            type: Array,
        },
        imgBaseUrl: {
            type: String,
        },
        itineraryDetails: {
            type: Array,
        },
        itineraryDetailsById: {
            type: Object,
        },
    },
    data() {
        return {};
    },
};
</script>

